import { Component, OnInit, OnDestroy } from '@angular/core';
import { DataServiceService } from "../../data-service.service";
import 'rxjs/add/observable/interval';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';
var alertify = require("alertifyjs");

@Component({
  selector: 'app-quizzes',
  templateUrl: './quizzes.component.html',
  styleUrls: ['./quizzes.component.scss']
})


export class QuizzesComponent implements OnInit,OnDestroy {
  questions;
  questions_answered: number = 0;
  questions_correct: number = 0;
  questions_wrong:number = 0;
  check_answer: boolean = false;
  current_question_index: number = 0  ;
  active_question: Question;
  user_answer: number;
  active_answer = "active";
  inactive_answer = "inactive";
  question_answer_status:boolean = false;
  active_button: boolean = true;
  quiz_length: number = 0;
  attempt_no: any;
  overlay: boolean = true;
  timer: number = 1000;
  overlay_finished:boolean = false;

  constructor(private data_service: DataServiceService, private toastr: ToastrService) { }

  ngOnInit() {
    this.getQuestions();
  }
  ngOnDestroy(){

  }
  checkAnswer(){
    this.check_answer = true;
  }

  getQuestions(){
    let subtopic_id = localStorage.getItem("subtopic")
    this.data_service.getQuizQuestions(subtopic_id).subscribe((response: any[]) => {
      console.log(response);
      this.quiz_length = response.length;
      this.questions = response;
      this.current_question_index = 0;
      this.active_question = this.questions[this.current_question_index];
    });
  }
  nextQuestion(){
    console.log("clicked");
    this.check_answer = false;

    // this.current_question_index += 1;

    if(this.questions[this.current_question_index].answer == this.user_answer){
      this.questions_answered += 1;
      this.questions_correct += 1;
      this.current_question_index += 1;
      console.log(this.current_question_index);
      console.log(this.questions[this.current_question_index]);
      this.data_service.commitQuizAnswer(this.user_answer, 1, this.questions[this.current_question_index].id).subscribe(response => {
        console.log("section 2");
        console.log(response);
      // this.current_question_index += 1;
      })
      console.log("section 1")
    } else {
      this.questions_answered += 1;
      this.questions_wrong += 1;
      this.data_service.commitQuizAnswer(this.user_answer, 0, this.questions[this.current_question_index].id).subscribe(response => {
        console.log("section 2");
        console.log(response);
      this.current_question_index += 1;
      })

    }
    if(this.questions[this.current_question_index] == undefined){
      alert("You have completed the quiz");
    }
    this.user_answer = 0;
    this.question_answer_status = false;
    // this.questions_answered += 1;
    // this.current_question_index += 1;
    console.log(this.current_question_index);
    this.active_question = this.questions[this.current_question_index];

    console.log(this.active_question);
    console.log(this.current_question_index);
    // this.data_service.answerCurrentQuestion().subscribe(response => {
    //   console.log(response);
    //   this.active_question = this.questions[this.current_question_index + 1];

    // })
  }
  startSession(){
    let date = Date.now();
    console.log(date);
  }
  endSession(){


  }
  highlightAnswer(id: number){
    if(this.question_answer_status == false){
      this.active_button = false;
      this.user_answer = id;
    } else {

    }

  }
  beginQuiz(){
    this.overlay = false;
    this.startTimer();
  }
  startTimer(){
    this.timer += Date.now();
    Observable.interval(1000)
    .subscribe((val) => { this.timer -= 1000;
    console.log() });
  }
  retakeQuiz(){
    this.current_question_index = 0;
    this.overlay = true;
    this.overlay_finished = false;
    this.questions_correct = 0;
    this.questions_answered = 0;
    this.questions_wrong = 0;
    this.active_question = this.questions[this.current_question_index];
    this.highlightAnswer(0);

  }
  showSuccess() {
    this.toastr.success('Quiz has been submitted');
  }
  submitQuiz(){
    console.log(localStorage.getItem('user_id'));
    this.overlay_finished = true;
    this.data_service.submitQuiz(this.questions_correct, this.quiz_length).subscribe(response => {
      console.log(response);
      this.showSuccess();
    })
  }

}
class Question {
  id: number;
  question: String;
  answer: number;
  option_a: string;
  option_b: string;
  option_c: string;
  option_d: string;
  option_a_explanation: string;
  option_b_explanation: string;
  option_c_explanation: string;
  option_d_explanation: string;
}
