import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { CKEditorModule } from '@ckeditor/ckeditor5-angular';
import { RouterModule, Routes } from '@angular/router';
import { SubtopicComponent } from './subtopic.component';
import { NestedNotesComponent } from './nested-notes/nested-notes.component';
import { ForumComponent } from './forum/forum.component';
import { TeacherNotesComponent } from './teacher-notes/teacher-notes.component';
import { QuizzesComponent } from './quizzes/quizzes.component';
import { QueryComponent } from './query/query.component';
import {MatTabsModule} from '@angular/material/tabs';
import {MatSidenavModule} from '@angular/material/sidenav';
import { AssignmentsComponent } from './assignments/assignments.component';
import { FormArray } from '@angular/forms';

const routes: Routes = [
  {
    path: ':subject_name',
    component: SubtopicComponent,
    children: [
      {
        path: 'notes',
        component: NestedNotesComponent,
      },
      {
        path: 'teacher_notes',
        component: TeacherNotesComponent
      },
      {
        path: 'quizzes',
        component: QuizzesComponent
      },
      {
        path: 'consult',
        component: QueryComponent
      },
      {
        path: 'forum',
        component: ForumComponent
      },
      {
        path:'assignments',
        component: AssignmentsComponent
      }
    ]
  }
]
@NgModule({
  declarations: [
    SubtopicComponent,
    AssignmentsComponent,
    NestedNotesComponent,
    QueryComponent,
    QuizzesComponent,
    ForumComponent,
    TeacherNotesComponent,
    AssignmentsComponent
  ],
  imports: [
    CommonModule,
    CKEditorModule,
    MatSidenavModule,
    MatTabsModule,
    RouterModule.forChild(routes)
  ]
})
export class SubtopicModule { }
