import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, NavigationEnd} from '@angular/router';
import {DataServiceService } from "../../data-service.service";
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';

import { filter } from "rxjs/operators";
@Component({

  selector: 'app-nested-notes',
  templateUrl: './nested-notes.component.html',
  styleUrls: ['./nested-notes.component.scss']
})
export class NestedNotesComponent implements OnInit {
  subtopic_id:string;
  topics: any;
  subject_fillable: any;
  notes:any;
  constructor(private activatedRoute: ActivatedRoute, private data_service: DataServiceService, private spinner: NgxSpinnerService, private router:Router) {

    console.log("loaded");
    this.router.events
      .pipe(filter(event => event instanceof NavigationEnd))
      .subscribe(() => {
        this.subtopic_id = localStorage.getItem('subtopic');
        console.log("triggered route event");
        this.getNotes();
      });

  }

  ngOnInit() {
    this.subtopic_id = localStorage.getItem('subtopic');
    console.log("loaded");
    this.getNotes();
  }



  getNotes(){
    console.log("subtopic id is" + this.subtopic_id);
    this.data_service.getNotes(this.subtopic_id).subscribe(response => {
      console.log("My notes response" + response);
      this.subject_fillable = response;
      this.notes = "";
      this.notes = response[0].notes;

      console.log(this.notes);
      console.log(response);
    })
  }
}
